* {
  box-sizing: border-box; }

html {
  overflow: hidden;
  font-family: "Roboto", sans-serif; }

body {
  min-height: 100vh;
  height: 100%;
  background: linear-gradient(45deg, #042422, #0f8078, #17c4b9, #ee6352);
  background-size: 800% 100%;
  background-position: 50% 0%;
  color: #fff; }
  @media (max-width: 400px) {
    body {
      font-size: 12px; } }

header, footer, main {
  text-align: center; }

.container {
  min-height: 100vh; }

header {
  font-family: "Oswald", sans-serif;
  text-shadow: 2px 4px 1px #0f8078;
  font-size: 4em;
  font-weight: 700;
  color: #fffff2;
  min-height: 25vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  @media (max-width: 768px) {
    header {
      min-height: 20vh;
      padding-top: 5vh;
      padding-bottom: 5vh; } }
  @media (max-width: 400px) {
    header {
      min-height: 10vh;
      padding-top: 8vh;
      padding-bottom: 2vh; } }

.this {
  font-weight: 400;
  font-size: 0.9em;
  text-shadow: none;
  font-family: "Roboto Mono", monospace;
  color: #ee6352;
  text-shadow: 0 1px rgba(0, 0, 0, 0.3);
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  margin-bottom: -0.3em;
  margin-left: 0.3em;
  margin-right: 0.3em; }

main {
  min-height: 65vh;
  margin: auto;
  width: 38vw; }
  @media (max-width: 1024px) {
    main {
      width: 80vw; } }
  @media (max-width: 768px) {
    main {
      min-height: 75vh;
      width: 90vw; } }
  @media (max-width: 400px) {
    main {
      padding-top: 10%;
      min-height: 85vh; } }

footer {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  min-height: 10vh;
  padding-bottom: 2em;
  text-align: center;
  width: 100vw;
  font-size: 0.85em; }
  footer a {
    display: inline-block;
    text-decoration: none;
    color: #fffff2; }
    footer a:hover {
      transform: translatey(-1px); }
    footer a:active {
      transform: translatey(1px); }

.codesnippet {
  margin-bottom: 30px;
  -webkit-box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.7);
  -moz-box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.7);
  box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.7); }

.options {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: space-between; }
  @media (max-width: 768px) {
    .options {
      justify-content: space-around;
      align-content: space-around; } }

button {
  position: relative;
  color: #fffff2;
  height: 3em;
  display: flex;
  align-items: center;
  justify-content: center;
  font: inherit;
  color: inherit;
  cursor: pointer;
  background-color: #ee6352;
  width: 48%;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  font-size: 1.2em;
  font-family: "Roboto Mono", monospace;
  font-weight: 700;
  border-radius: 0.3em;
  border: none;
  -webkit-box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.3);
  box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.3);
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  text-shadow: 0 1px rgba(0, 0, 0, 0.3);
  transition: background-color 0.2s ease; }
  button:focus {
    border: none;
    outline: none; }
  button:hover {
    background-color: #f07869;
    border: none; }
  button:active {
    transform: translate(0px, 2px);
    background-color: #ec4e3b;
    border: none; }
  @media (max-width: 768px) {
    button {
      font-size: 1em; } }

.card__button {
  justify-content: space-between; }

.card__button__helper-icon {
  font-size: 2em;
  width: 1em; }

.card__button__text {
  text-align: center;
  width: 100%; }

.mask {
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 10; }

.hidden {
  display: none; }

h1 {
  font-family: "Oswald", sans-serif;
  text-shadow: 1px 1px 1px #6c8398;
  font-size: 2em;
  font-weight: 700; }

.card {
  overflow: auto;
  border-radius: 3px;
  padding-top: 30px;
  padding-left: 60px;
  padding-right: 60px;
  padding-bottom: 30px;
  z-index: 20;
  background-color: #fffff2;
  min-height: 20vh;
  width: 35vw;
  position: fixed;
  transition: all 0.5s ease-out;
  line-height: 1.3;
  color: #2C363F;
  -webkit-box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.9);
  -moz-box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.9);
  box-shadow: 14px 17px 44px -18px rgba(0, 0, 0, 0.9); }
  .card button {
    font-size: 1em; }
  .card a {
    text-decoration: none;
    color: #ee6352; }
  .card p {
    text-align: justify; }
  .card a.close {
    position: absolute;
    font-size: 3em;
    color: #2C363F;
    opacity: 0.4;
    font-size: 2em;
    font-weight: 700;
    top: 0.5em;
    right: 0.5em; }
    .card a.close:hover {
      cursor: pointer;
      opacity: 0.6; }
    .card a.close:active {
      opacity: 0.8;
      transform: translatey(2px); }
  @media (max-width: 1024px) {
    .card {
      width: 95vw;
      width: 95vw; } }
  @media (max-width: 768px) {
    .card {
      max-height: 95%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); } }
  @media (min-width: 768px) {
    .card {
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); } }
  .card .card__options {
    margin-top: 2em;
    margin-bottom: 2em;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: space-between; }
    .card .card__options button {
      color: #fffff2; }

dt {
  margin-top: 5px;
  margin-bottom: 5px; }

pre[class*="language-"] {
  padding: 1em 3em; }
  @media (max-width: 768px) {
    pre[class*="language-"] {
      padding: 1em 2em; } }
